import XEUtils from 'xe-utils'

export function getRest (data) {
  if (data && data.x && XEUtils.isString(data.x)) {
    return XEUtils.toStringJSON(decodeURIComponent(data.x))
  }
  return data
}

export default {
  getRest,
  getCompType (type) {
    return type === 'src' ? '' : ''
  },
  getCompExpiration (deadline) {
    return deadline > 0 ? '1' : ''
  }
}
